<template>
  <v-dialog v-model="dialog" persistent max-width="330">
    <v-card>
      <v-card-title class="headline">Delete Post</v-card-title>
      <v-card-text>
         <br>
         ?
      </v-card-text>
      <v-card-actions>
        <v-spacer></v-spacer>
        <v-btn color="red darken-1" text @click="delPost()"></v-btn>
        <v-btn color="blue darken-3" text @click="close()"></v-btn>
      </v-card-actions>
    </v-card>
  </v-dialog>
</template>
<script>
export default {
  props: [ 'dialog', 'post' ],
  methods: {
    close () {
      this.$emit('close')
      this.$emit('getPosts')
    },
    delPost () {
      this.$axios.delete(`/post/${this.post._id}`)
        .then((r) => {
          this.$store.commit('pop', { msg: ' ', color: 'success' })
          this.close()
        })
        .catch((e) => {
          this.$store.commit('pop', { msg: e.message, color: 'error' })
        })
    }
  }
}
</script>
